.portfolio-block .item {
  position: relative;
  padding: 15px;
  overflow: hidden;
  border-radius: 0.25rem;
  margin: 15px 0 15px 0; }

.portfolio-block .item > a {
  background: rgba(55, 119, 188, 0.7);
